// Site Variables
//

// Colors By Name
// =====================
$black: #000;
$bright-blue: #2793e0;
$dark-gray: #999;
$pure-orange: #ffb900;
$strong-blue: #0055b8;
$strong-lime-green: #35c621;
$strong-pink: #d8006d;
$very-dark-gray: #333;
$very-light-gray: #eee;
$vivid-orange: #ff9030;
$white: #fff;

// Colors By Usage
// =====================
$color-shadow: rgba(0, 0, 0, .2);
$color-accent: $pure-orange;
$color-primary: $bright-blue;
$color-background: $white;
$color-background-shade: $very-light-gray;
$color-text: $very-dark-gray;
$color-text-invert: $white;
$color-text-weak: $dark-gray;
$color-text-link: $color-primary;

// Colors By Age Group
// =====================
$color-baby: $strong-pink;
$color-toddler: $vivid-orange;
$color-pre-schooler: $pure-orange;
$color-grade-schooler: $strong-lime-green;
$color-pre-teen: $strong-blue;

// Font Families
// =====================
$font-family-text: 'open-sans', sans-serif;
$font-family-display: 'museo-sans-rounded', sans-serif;

// Font Weights
// =====================
$font-weight-semibold: 600;
$font-weight-extrablack: 900;

// Font Sizes
// =====================
$font-size-x-small-mobile: 11;
$font-size-x-small: 13;
$font-size-small-mobile: 12;
$font-size-small: 16;
$font-size-regular-mobile: 15;
$font-size-regular: 18;
$font-size-large-mobile: 18;
$font-size-large: 21;
$font-size-x-large-mobile: 24;
$font-size-x-large: 30;
$font-size-xx-large-mobile: 40;
$font-size-xx-large: 80;

// Grid Settings
// See http://thoughtbot.github.io/neat-docs/latest
// =====================
$column: 70px;
$gutter: 40px;
$grid-columns: 12;
$max-width: 1280px;

// Breakpoints
// =====================
$bp-small: new-breakpoint(min-width 375px);
$bp-large: new-breakpoint(min-width 1000px);
$breakpoint-font: $bp-large;

// Heights
// =====================
$height-banner: 50px;
$height-banner-large: 60px;
$height-banner-logo: 60px;

// Widths
// =====================
$width-banner-logo: 75px;
$width-offcanvas-side: 300px;
